<template>
    <div v-if="state.token">
        {{state}}<br>
        <h3>콜백 화면</h3>
    </div>
    <div style="height:500px;">
    </div>
</template>

<script>

import axios from 'axios'
import { onMounted, reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

export default {
    setup () {
        const store = useStore();
        const router = useRouter();
        const state = reactive({
            query : window.location.search,
            id : "NorOMfFixWqv2LMj8T2X",
            secret : "PY684GXakz",
            token : null,
            data : null
        })

        onMounted(async()=>{
            navertoken()
        })

        const navertoken = async() => {
            const url =`/oauth2.0/token${state.query}&grant_type=authorization_code&client_id=${state.id}&client_secret=${state.secret}`
            const headers = {'X-Naver-Client-Id':state.id, 'X-Naver-Client-Secret': state.secret}
            const data = await axios.get(url,{headers})
            if(data.status === 200){
                state.token = data.data.access_token
                memberinfo();
            }
        }
        const memberinfo = async() => {
            const url = '/v1/nid/me'
                const headers = {'Authorization' : "Bearer "+state.token }
                const {data} = await axios.get(url,{headers})
                if(data.message === 'success'){
                    state.data = data.response
                    save()
                }
        }

        const save = async() => {
            const url = `/BOOT1/member/naver.json`
            const headers = {"Content-Type":"application/json"}
            const body = {
                userid : state.data.id,
                gender : state.data.gender,
                phone : state.data.mobile.split("-")[0]+state.data.mobile.split("-")[1]+state.data.mobile.split("-")[2],
                birth : state.data.birthyear,
                nickname : state.data.nickname,
                social : "naver"
            }
            const {data} = await axios.post(url,body,{headers})
            if(data.status === 200){
                if(data.result === "banned user"){
                    alert("차단된 유저입니다.")
                    router.push({path:'/login'})
                }
                else{
                    sessionStorage.setItem("token",data.result)
                    sessionStorage.setItem("role",data.role)
                    sessionStorage.setItem("imageno",data.imageno)
                    sessionStorage.setItem("userid",data.userid)
                    sessionStorage.setItem("nickname",data.nickname)
                    sessionStorage.setItem("social",data.social)

                    store.commit("setLogged",true)
                    store.commit("setRole",data.role)
                    store.commit("setToken",data.result)
                    store.commit("setUserid",data.userid)
                    store.commit("setNickname",data.nickname)
                    store.commit("setImageno",data.imageno)
                    store.commit("setSocial",data.social)
                    router.push({path:'/main'})
                }
            }
        }

        return {
            ...toRefs(state),state
        }
    }
}
</script>

<style lang="scss" scoped>

</style>